"use client"
import { useState } from 'react';

export default function MortgageCalculator() {
    const [taxes, setTaxes] = useState(265);
    const [zip, setZip] = useState('421005');
    const [result, setResult] = useState(null);

    const calculateMortgage = (e) => {
        e.preventDefault();
        // Dummy calculation logic; replace with real logic as needed
        const calculatedValue = Number(taxes) * 1000;
        setResult(calculatedValue);
    };

    return (
        <div>
            <h1>Mortgage Calculator</h1>
            <form onSubmit={calculateMortgage}>
                <div>
                    <label>Taxes: </label>
                    <input
                        type="number"
                        value={taxes}
                        onChange={(e) => setTaxes(e.target.value)}
                    />
                </div>
                <div>
                    <label>Zip Code: </label>
                    <input
                        type="text"
                        value={zip}
                        onChange={(e) => setZip(e.target.value)}
                    />
                </div>
                <button type="submit">Calculate</button>
            </form>
            {result && <p>Estimated Mortgage: ${result}</p>}
        </div>
    );
}